
* {
  margin: 0;
  padding: 0;
  color: #404040;
  font-family: '\5FAE\8F6F\96C5\9ED1light';
}
ul, ol, li {
  list-style: none;
}
a {
  text-decoration: none;
}
body {
  width: 100%;
  height: 100%;
}


.banner {
  margin: 0 2px;
  height: 80px;
  background: #99ccff;
}
.banner-message {
  font-size: 40px;
  line-height: 80px;
  padding-left: 80px;
  color: #ffffff;
}
.banner img {
  vertical-align: sub;
}

.message {
  margin: 10px 2px;
  min-height: 620px;
  border: 1px solid black;
  position: relative;
}
.left {
  position: absolute;
  left: 0px;
}
.right {
  position: absolute;
  right: 0px;
}

.message-list {
  height: 600px;
  padding: 10px 30px;
}
.border {
  border: 1px solid #cccccc;
  border-radius: 14px;
  padding: 5px 5px;
  overflow: hidden;
}
.itemfade-enter,
.itemfade-leave-active {
  opacity: 0;
}
.chat-list {
  height: 500px;
  overflow-x: hidden;
  overflow-y: scroll;
}
.chat-list-title {
  height: 30px;
  background: #CCCCCC;
  color: #ffffff;
  line-height: 30px;
  padding-left: 25px;
  font-size: 18px;
  border-radius: 14px;
}
.chat-list-title img {
  vertical-align: sub;
}
.chat-send {
  padding: 10px;
}
.chat-input {
  width: 638px;
  height: 27px;
}
.chat-send-button {
  display: inline-block;
  padding: 6px 12px;
  margin-bottom: 0;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.42857143;
  text-align: center;
  white-space: nowrap;
  vertical-align: middle;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-image: none;
  border: 1px solid transparent;
  border-radius: 4px;
  color: #333;
  background-color: #fff;
  border-color: #ccc;
  width: 80px;
}
.chat-send-button:hover {
  color: #333;
  background-color: #e6e6e6;
  border-color: #adadad;
}

.user-list {
  height: 600px;
  padding: 10px 30px;
  width: 300px;
}
.border {
  height: 600px;
  border: 1px solid #cccccc;
  border-radius: 14px;
  padding: 5px 5px;
  overflow: hidden;
}
.user-list-title {
  height: 30px;
  background: #CCCCCC;
  color: #ffffff;
  line-height: 30px;
  padding-left: 25px;
  font-size: 18px;
  border-radius: 14px;
}
